import React from 'react';
import PropTypes from 'prop-types';
import styled, { ThemeProvider, css } from 'styled-components';
import Helmet from 'react-helmet';

import Footer from '../Footer';
import Latest from '../Latest';
import Header from '../Header';
import { GlobalStyles, themes, styleHelpers } from '../../styles';
import { useLocalStorage } from '../../hooks';

const contentStyles = ({ theme, location }) => css`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 30px;
  max-width: 960px;
  background-color: ${theme.colors.p000};
  flex-grow: 1;
  transition: color ${theme.transitions.default},
    background ${theme.transitions.default};
  min-height: 60vh;

  ${styleHelpers.media.medium`
    margin: 0 50px;
  `};

  ${styleHelpers.media.large`
    margin: 0 auto;
  `};
`;

const contentHomeStyles = ({ location }) =>
  location.pathname === '/' &&
  css`
    max-width: 100%;
    margin: 0;
  `;

const Content = styled('div')`
  ${contentStyles}${contentHomeStyles}
`;

class Layout extends React.Component {
  state = {
    isDark: false,
  };

  componentDidMount() {
    // Getting dark mode value from localStorage!
    const lsDark = JSON.parse(localStorage.getItem('isDark'));
    if (lsDark) {
      this.setState({ isDark: lsDark });
    }
  }

  toggleDark = checked => {
    localStorage.setItem('isDark', JSON.stringify(checked));
    this.setState({ isDark: checked });
  };

  render() {
    const { isDark } = this.state;
    const { location, children } = this.props;
    const { darkTheme, mainTheme } = themes;

    return (
      <ThemeProvider theme={isDark ? darkTheme : mainTheme}>
        <>
          <Helmet
            meta={[
              {
                name: 'theme-color',
                content: isDark ? darkTheme.colors.p000 : mainTheme.colors.p200,
              },
            ]}
          />
          <GlobalStyles />
          <Header
            location={location}
            onModeSwitch={checked => this.toggleDark(checked)}
            isDark={isDark}
          />
          <Content location={location}>{children}</Content>
          <Footer>
            <Latest />
          </Footer>
        </>
      </ThemeProvider>
    );
  }
}

